import { serializeAs, deserializeAs, serialize } from 'cerialize';

export class User {

    @serializeAs('id')
    @deserializeAs('id')
    private _id: string;

    @serializeAs('name')
    @deserializeAs('name')
    private _name: string;

  
    @serializeAs('email')
    @deserializeAs('email')
    private _email: string; 
  
    @serializeAs('userName')
    @deserializeAs('userName')
    private _userName: string;
    
    @serializeAs('password')
    private _password: string;
    
    @serializeAs('role')
    @deserializeAs('role')
    private _role: Array<object>;

    

    /**
     * Getter name
     * @return {string}
     */
	public get name(): string {
		return this._name;
	}

    /**
     * Getter email
     * @return {string}
     */
	public get email(): string {
		return this._email;
	}

    /**
     * Getter userName
     * @return {string}
     */
	public get userName(): string {
		return this._userName;
	}

    /**
     * Getter password
     * @return {string}
     */
	public get password(): string {
		return this._password;
	}

    /**
     * Getter role
     * @return {Array<object>}
     */
	public get role(): Array<object> {
		return this._role;
	}

    /**
     * Setter name
     * @param {string} value
     */
	public set name(value: string) {
		this._name = value;
	}

    /**
     * Setter email
     * @param {string} value
     */
	public set email(value: string) {
		this._email = value;
	}

    /**
     * Setter userName
     * @param {string} value
     */
	public set userName(value: string) {
		this._userName = value;
	}

    /**
     * Setter password
     * @param {string} value
     */
	public set password(value: string) {
		this._password = value;
	}

    /**
     * Setter role
     * @param {Array<object>} value
     */
	public set role(value: Array<object>) {
		this._role = value;
	}
    

    /**
     * Getter id
     * @return {string}
     */
	public get id(): string {
		return this._id;
	}

    /**
     * Setter id
     * @param {string} value
     */
	public set id(value: string) {
		this._id = value;
	}
    
}   